footer{
    align-items: center;
    /*
        그라데이션 주기 > 어떤색부터 어떤색까지 서서히
    */
    background-image: linear-gradient(#e6e6e6,#fff);
    /* inset: 오프셋을 정의하지 않는다 */
    box-shadow: inset 0 5px 12px rgba(0,0,0,.3);
    display: flex !important;
    justify-content: center;
    /* sticky: 부모 요소를 따라가며 고정된다 */
    position: sticky;
    height: 120px;
    padding: 0 144px;
    /* calc: 내부 연산의 결과를 속성값으로 적용한다 */
    top: calc(100vh - 120px);
  }
  footer > p{
    color: #09473F;
    font-size: 24px;
  }
  footer > p > strong {
    font-weight: 700;
  }