.signin{
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    /* background-color: #e7473c; */
    background-image: url('../images/potter.jpg');
    /* 흐림 효과를 위한 색깔 지정 및 효과 적용*/
    background-color: gray;
    background-blend-mode: multiply;
	
	/* 선명도 조절 0부터 1 */
	  /* opacity: 0.7; */
  }
  .signin > .signin__wrapper{
    width: 50vh;
    height: 50vh;
    /* background-color: #f0eff0; */
    background-image: url(../images/dobby.jpg);
    border: none;
    box-shadow: 0 20px 15px rgb(41, 0, 0);
  }
  /* 로그인 폼 */
  .signin > .signin__wrapper > .signin__form{
    height: 60%;
  }
  .signin > .signin__wrapper > .signin__form > form{
    padding-top: 70px;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  .signin > .signin__wrapper > .signin__form > form > input,
  .signin > .signin__wrapper > .signin__form > form > button {
    width: 80%;
    height: 40px;
    border: none;
  }
  .signin > .signin__wrapper > .signin__form > form > input{
    margin-bottom: 15px;
    font-size: 14px;
    padding-left: 10px;
    background-color: #e6e1e1;
  }
  .signin > .signin__wrapper > .signin__form > form > button{
    font-size: 22px;
    font-weight: 600;
    color: #ffffff;
    background-color: #E17923;
  }
  
  .signin > .signin__wrapper > .signin__register{
    font-size: 18px;
    text-align: center;
    margin: 70px;
  }
  .signin > .signin__wrapper > .signin__register > p > .signin__register__create{
    cursor: pointer;
    color: #e7473c;
    font-weight: 800;
  }
  .signin > .signin__wrapper > .signin__register > p > .signin__register__create:hover{
    color: #3d3a3a; 
  }