main{
    background-color:  #e7473c;
    margin-left: 33%
  }
  main > .movie{
    height: 100vh;
    padding: 12% 10%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color:white;
  }
  main > .movie > .wrapper{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
  }
  main > .movie > .wrapper > .movie__header {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    margin-bottom: 25px;
  }
  main > .movie > .wrapper > .movie__header > h4{
    font-size: 28px;
  }
  main > .movie > .wrapper > .movie__header > h5{
    font-size: 18px;
    letter-spacing: 2px;
  }
  main > .movie > .wrapper > .movie__video{
    width: 100%;
    height: 100%;
  }
  
  main > .movie > .wrapper > .movie__video > iframe{
    width: 100%;
    height: 100%;
  }