header {
    position: fixed;
    top: 0;
    background-color: #f0eff0;
    width: 33%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    color: #09473F;
}

header > h1 {
    font-size: 45px;
    font-weight: 700;
    margin-left: 35px;
    margin-top: 40px;
    width: 50%;
    letter-spacing: 5px;
  }
  .bottom > h4 {
    font-size: 14px;
    font-weight: 700;
    margin-top: 90px;
    margin-left: 35px;
  }
  .bottom > h4, .bottom > p {
    width: 70%;
    line-height: 1.2;
    text-align: justify;
    margin-left: 35px;
    
  }
  .bottom > p {
    font-weight: 300;
    font-size: 13px;
    color: #09473F;
    margin-bottom: 40px;
    margin-top: 30px;
  }
  .bottom > nav > ul {
    width: 90%;
    display: flex;
    flex-wrap: wrap;
    margin-left: 35px;
  }
  .bottom > nav > ul > li {
    cursor: pointer;
    margin-right: 12px;
    font-size: 15px;
    color: #000000;
    opacity: 0.5;
    border-bottom: 1px solid rgba(0, 0, 0, 1);
    margin-bottom: 20px;
  }
  .bottom > nav > ul > li:hover {
    opacity: 1;
  }